import ApexChart from "react-apexcharts";
import styled from "styled-components";
import { useEffect, useState } from "react";
import { useRecoilValue } from "recoil";
import { isDarkAtom } from "../atoms";

interface ChartProps {
  coinId: string;
}
interface IHistorical {
  time_open: string;
  time_close: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
  market_cap: number;
}

export default function Price({ coinId }: ChartProps) {
  const BASE_URL = `https://ohlcv-api.nomadcoders.workers.dev`;
  const isDark = useRecoilValue(isDarkAtom);
  const [data, setData] = useState<IHistorical[]>([]);
  const [isLoading, setisLoading] = useState(true);
  useEffect(() => {
    (async () => {
      const response = await fetch(`${BASE_URL}?coinId=${coinId}`);
      const json = await response.json();

      setData(json);
      setisLoading(false);
    })();
  }, []);

  return (
    <div>
      {isLoading ? (
        "Loading chart..."
      ) : (
        <>
          <CoinName>{coinId}</CoinName>
          <ApexChart
            type="line"
            series={[
              {
                name: "Price",
                data: data?.map((price) => price.close) ?? [],
              },
            ]}
            options={{
              theme: {
                mode: isDark ? "dark" : "light",
              },
              chart: {
                height: 300,
                width: 500,
                toolbar: {
                  show: false,
                },
                background: "transparent",
              },
              grid: { show: false },
              stroke: {
                curve: "smooth",
                width: 4,
              },
              yaxis: {
                show: false,
              },
              xaxis: {
                type: "datetime",
                categories: data?.map((price) => price.time_close),
              },
              fill: {
                type: "gradient",
                gradient: { gradientToColors: ["#0be881"], stops: [0, 100] },
              },
              colors: ["#0fbcf9"],
              tooltip: {
                y: {
                  formatter: (value) => `$${value.toFixed(2)}`,
                },
              },
            }}
          />
        </>
      )}
    </div>
  );
}

const CoinName = styled.div`
  font-size: 1.5em;
  text-align: center;
  font-weight: bold;
  color: ${(props) => props.theme.accentColor};
`;
