import ReactApexChart from "react-apexcharts";
import styled from "styled-components";
import { useEffect, useState } from "react";
import { useRecoilValue } from "recoil";
import { isDarkAtom } from "../atoms";

interface ChartProps {
  coinId: string;
}
interface IHistorical {
  time_open: string;
  time_close: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
  market_cap: number;
}

export default function Chart({ coinId }: ChartProps) {
  const BASE_URL = `https://ohlcv-api.nomadcoders.workers.dev`;
  const isDark = useRecoilValue(isDarkAtom);
  const [data, setData] = useState<IHistorical[]>([]);
  const [isLoading, setisLoading] = useState(true);
  useEffect(() => {
    (async () => {
      const response = await fetch(`${BASE_URL}?coinId=${coinId}`);
      const json = await response.json();
      setData(json);
      setisLoading(false);
    })();
  }, []);

  return (
    <>
      {isLoading ? (
        "Loading chart..."
      ) : (
        <Wrapper>
          <CoinName>{coinId}</CoinName>
          <ReactApexChart
            type="candlestick"
            name="candle"
            series={[
              {
                data: data?.map((price) => [
                  new Date(price.time_open).getTime(),
                  price.open,
                  price.high,
                  price.low,
                  price.close,
                ]) as any,
              },
            ]}
            options={{
              theme: {
                mode: "light",
              },
              chart: {
                type: "candlestick",
                height: 350,
                width: 500,
                toolbar: {
                  show: false,
                },
                background: "transparent",
              },
              stroke: {
                curve: "smooth",
                width: 2,
              },
              yaxis: {
                show: false,
              },
              xaxis: {
                type: "datetime",
                categories: data?.map((price) => price.time_close),
                labels: {
                  style: {
                    colors: "#9c88ff",
                  },
                },
              },
              plotOptions: {
                candlestick: {
                  colors: {
                    upward: "#137bf9",
                    downward: "#f94513",
                  },
                },
              },
            }}
          />
        </Wrapper>
      )}
    </>
  );
}
const Wrapper = styled.div`
  width: 440px;
  height: 500px;
`;

const CoinName = styled.div`
  font-size: 1.5em;
  text-align: center;
  font-weight: bold;
  color: ${(props) => props.theme.accentColor};
`;

const Img = styled.img`
  margin: 5%;
  width: 90%;
  height: 40%;
`;

const BtnWrapper = styled.div`
  margin-top: 2%;
  margin-left: 24%;
`;
